import React from 'react'
import img1 from './img/delete.png'

function TodoList({text, todo, todos, setTodos}) {
    const handleDelete = () => {
        const todoClass = document.querySelector('.todo');
        todoClass.classList.add('trash')
        setTimeout(function(){
            setTodos(todos.filter((el) => el.id !== todo.id))
        },500)
        
    }
    const handleComplete = () => {
        setTodos(
            todos.map(item => {
                if (item.id === todo.id){
                    return {
                        ...item,
                        completed: !item.completed
                    }
                }
                return item;
            })
        )
    }
    return (
        <div className='todo'>
            <li className={`todo-list ${todo.completed ? "completed" : ""}`}><span>{text}</span></li>
            <button onClick={handleComplete} className={`completeBtn  ${todo.completed ? "completedBtn" : ""}`}>✓</button>
            <button onClick={handleDelete} className='trashBtn'><img src={img1}/></button>
        </div>
    )
}

export default TodoList
