import React, { useEffect } from 'react'
import './Todo.scss'
import TodoList from './TodoList';


function Todo({text, setText, todos, setTodos, status, setStatus, filteredTodos}) {
    useEffect(()=> {
        const input1 = document.querySelector('input')
        input1.focus(); 
    },[])
    const handeleSubmit = (e) => {
        e.preventDefault();
        setTodos([
            ...todos,
            { text1: text , completed: false, id: Math.random() * 1000} 
          ])
        setText('')
    }
    const handleFilter = (e) => {
        setStatus(e.target.value)
    }
    return (
        <div className='form-container'>
            <form onSubmit={handeleSubmit}>
                <h1 className='h1'>To-Do-List</h1>
                <div className='inputDiv'>
                    <input className='input' type='text' value={text} onChange={e => setText(e.target.value)} required/>
                    <button className='inputBtn' type='submit' ><span className='btnSpan1'></span><span className='btnSpan2'></span></button>
                </div>
                <div className='select'>
                    <h1>To-Do :  </h1>
                    <select name='todos' onChange={handleFilter} className='filterTodos'>
                        <option value='all'>All</option>
                        <option value='completed'>Completed</option>
                        <option value='uncompleted'>Uncompleted</option>
                    </select>
                </div>
                <h1 className='h2'>List : {status}</h1>
            </form> 
            <div id='div1'>
               <ul id='ul1'>
                    {
                        filteredTodos.map(todo => (
                        <TodoList
                             key={todo.id} 
                             todo={todo} 
                             todos={todos} 
                             setTodos={setTodos} 
                             text={todo.text1}
                        />))
                    }
               </ul>
            </div>
        </div>
    )
}

export default Todo
